import {ParamType} from '../../../../../../../@polygonjs/polygonjs/src/engine/poly/ParamType';
import {ParamOptionToAdd} from './../../../../../../../@polygonjs/polygonjs/src/engine/nodes/utils/params/ParamsController';
import {objectCloneDeep} from './../../../../../../../@polygonjs/polygonjs/src/core/ObjectUtils';

interface SetupOptions<T extends ParamType> {
	paramData: ParamOptionToAdd<T>;
	updateParent: (paramData: ParamOptionToAdd<T>) => void;
}

export function SetupFileBrowse<T extends ParamType>(options: SetupOptions<T>) {
	function updateFileBrowse(_paramData: ParamOptionToAdd<T>) {
		const paramData = objectCloneDeep(options.paramData);
		paramData.options = paramData.options || {};
		paramData.options.fileBrowse = _paramData.options?.fileBrowse;
		options.updateParent(paramData);
	}

	return {
		updateFileBrowse,
	};
}
