import {CoreType} from '../../../../../../@polygonjs/polygonjs/src/core/Type';
import {ParamType} from '../../../../../../@polygonjs/polygonjs/src/engine/poly/ParamType';
import {ParamOptionToAdd} from '../../../../../../@polygonjs/polygonjs/src/engine/nodes/utils/params/ParamsController';
import {Ref, watch} from 'vue';
interface LocalStorageOptions {
	paramTypesList: Ref<ParamOptionToAdd<ParamType>[]>;
	selectedParamIndex: Ref<number>;
}
interface LocalStorageData {
	selectedParamIndex: number;
}

const KEY = 'ParamsEditorCore-data';

export function SetupLocalStorage(options: LocalStorageOptions) {
	function saveToLocalStorage() {
		const data: LocalStorageData = {
			selectedParamIndex: options.selectedParamIndex.value,
		};
		localStorage.setItem(KEY, JSON.stringify(data));
	}
	// function clearLocalStorage() {
	// 	localStorage.removeItem(KEY);
	// }

	watch(options.selectedParamIndex, saveToLocalStorage);

	// onMounted(loadFromLocalStorage);

	function loadFromLocalStorage() {
		const current = localStorage.getItem(KEY);
		if (current) {
			try {
				const currentData = JSON.parse(current) as Partial<LocalStorageData> | undefined;
				if (!currentData) {
					return;
				}
				const selectedParamIndex = currentData.selectedParamIndex;
				if (!(selectedParamIndex != null)) {
					return;
				}
				if (CoreType.isNumber(selectedParamIndex) && !CoreType.isNaN(selectedParamIndex)) {
					if (selectedParamIndex < options.paramTypesList.value.length) {
						options.selectedParamIndex.value = selectedParamIndex;
					}
				}
			} catch (err) {}
		}
	}

	return {
		// saveToLocalStorage,
		// clearLocalStorage,
		loadFromLocalStorage,
	};
}
