import {objectCloneDeep} from './../../../../../../@polygonjs/polygonjs/src/core/ObjectUtils';
import {ParamType} from './../../../../../../@polygonjs/polygonjs/src/engine/poly/ParamType';
import {ParamOptionToAdd} from './../../../../../../@polygonjs/polygonjs/src/engine/nodes/utils/params/ParamsController';
import {Ref} from 'vue';
import {BaseNodeType} from './../../../../../../@polygonjs/polygonjs/src/engine/nodes/_Base';

interface Options {
	node: BaseNodeType;
	editSpareParams: boolean;
	paramTypesList: Ref<ParamOptionToAdd<ParamType>[]>;
}

export function populateParamTypesList(options: Options) {
	const {node, editSpareParams, paramTypesList} = options;
	const params = editSpareParams ? node.params.spare : node.params.non_spare;
	paramTypesList.value.splice(0, paramTypesList.value.length);
	for (let param of params) {
		if (!param.parentParam()) {
			paramTypesList.value.push({
				name: param.name(),
				type: param.type(),
				rawInput: param.rawInputSerialized(),
				initValue: param.defaultValueSerialized(),
				options: objectCloneDeep(param.options.current()),
			});
		}
	}
}
